    import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
	export default {
		data() {
			return {
				storeinfo:[]
			}
		},
        computed: mapState(['sellerHasLogin']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		}, 
		methods: {   
			async loadData() {  
				let _this = this;
				if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'login',
					})
				}else{
					_this.$Request.post('seller_index/payrqcode').then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.storeinfo=res.datas.store_info;
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					}); 
				}
			},
			 
		}  
		   
	}
